.sidebar{
    flex: 3;
    margin: 20px;
    padding-bottom: 30px;
    background-image:inherit;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.sidebarItem{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.sidebarTitle{
    margin: 10px;
    padding: 5px;
    width: 80%;
    border-top: 1px solid #a7a4a4;
    border-bottom: 1px solid #a7a4a4;
    font-family: 'Varela Round', sans-serif;
    font-size: 12px;
    color: #222;
    font-weight: 600;
    line-height: 20px;
    text-align: center;
}
.sidebarItem > img {
    margin-top: 15px;
    width: 75%;
    height: 300px;
}
.sidebarItem > p {
    padding: 30px;
}
.sidebarSocial{
    margin-top: 15px;
    width: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.sidebarIcon {
    font-size: 16px;
    margin-left: 10px;
    cursor: pointer;
}
